* {
  padding: 0;
  margin: 0;
  outline: none;
}
*:focus,
*:active {
  outline: none;
}
*::-moz-focus-outer {
  border: 0;
}

input:focus {
  outline: none;
}

body {
  font-family: 'Nunito', sans-serif;
  font-weight: 300;
  color: white;

  background: fixed no-repeat center;
  background-size: cover;
  -webkit-animation: background-change 300s 0s infinite;
          animation: background-change 300s 0s infinite;
}

section.main-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section.audio-section {
  min-height: 85vh;
  height: 100%;
}

button {
  background: none;
  border: none;
}

button:hover {
  cursor: pointer;
}

header.header {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 15vh;

  -webkit-backdrop-filter: blur(2px);

          backdrop-filter: blur(2px);
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.089);
}

header.header h1 a {
  text-decoration: none;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.7);
}

div.audios {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 30px;
  gap: 30px;
}

@media (max-width: 650px) {
  div.audios {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 550px) {
  div.audios {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (max-width: 400px) {
  div.audios {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 300px) {
  div.audios {
    grid-template-columns: 1fr;
  }
}

div.sound-component {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100px;
  height: 100px;
}

div.sound-button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100px;
  height: 100px;

  opacity: 0.5;

  transition: 0.1s;
}
div.sound-button:hover {
  cursor: pointer;
  opacity: 0.8;
}
div.sound-button.selected {
  opacity: 1;
}

:root {
  --transition: 1s;
}

div#configurations {
  position: fixed;
  top: 0;
  right: -300px;

  height: 100%;
  min-width: 14vw;

  padding: 15px;

  transition: 1s;

  transition: var(--transition);
}

div#configurations div.fieldset div.fieldset-content {
  padding: 15px;
  text-align: center;
  color: #39353b;
}

div#configurations div.fieldset div.fieldset-content-title {
  border-radius: 10px 10px 0 0;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.7);
}

div#configurations div.fieldset div.fieldset-content-input {
  border-radius: 0 0 10px 10px;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background: rgba(255, 255, 255, 0.5);
}
div#configurations div.fieldset input#url {
  border: none;
  background: none;
  text-align: center;
}

div#configurations button {
  border-radius: 10px;

  padding: 15px;
  margin-top: 5px;

  width: 100%;

  -webkit-backdrop-filter: blur(5px);

          backdrop-filter: blur(5px);
  background: rgba(255, 255, 255, 0.5);
  color: rgba(0, 0, 0, 0.753);
  transition: 0.1s;
}

div#configurations button:hover {
  background-color: rgba(255, 200, 200, 0.5);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
div#configurations button:active {
  background-color: white;
}

#configButton {
  display: none;
}

#configLabel {
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 2vh;
  right: 2vw;

  width: 80px;
  height: 80px;

  color: rgba(255, 255, 255, 0.5);
  font-size: 30px;

  transition: 1s;

  transition: var(--transition);
}
#configLabel:hover {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.7);
}

#configButton:checked ~ #configLabel {
  transform: translateX(-14vw) rotateY(-180deg);
  color: rgba(255, 255, 255, 1);
}

#configButton:checked ~ #configurations {
  transform: translateX(-300px);
}

@media (max-width: 1100px) {
  #configLabel {
    transition: none;
  }
  #configButton:checked ~ #configLabel {
    transform: rotateY(-180deg);
    left: 0;
    color: rgba(255, 255, 255, 1);
  }

  div#configurations div.fieldset div.fieldset-content-title {
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
  }

  div#configurations div.fieldset div.fieldset-content-input {
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
  }
}

@media (max-width: 750px) {
  div#configurations {
    position: relative;
    right: 0;

    max-width: 250px;

    margin: auto;
  }

  div#configurations div.fieldset div.fieldset-content {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  div#configurations div.fieldset div.fieldset-content-input {
    min-height: 30px;

    border-radius: 0 0 10px 10px;

    -webkit-backdrop-filter: blur(5px);

            backdrop-filter: blur(5px);
    background: rgba(255, 255, 255, 0.5);
  }

  div#configurations button {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 50px;

    background: rgba(255, 200, 200, 0.5);
    color: rgba(0, 0, 0, 0.753);
  }

  div#configurations button:active {
    background-color: rgba(255, 200, 200, 0.8);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
  }

  #configButton {
    display: none;
  }
  #configLabel {
    display: none;
  }
}

input[type='range'].audio-controller {
  opacity: 0;

  -webkit-appearance: none;

     -moz-appearance: none;

          appearance: none;

  width: 100%;
  height: 2px;

  background: white;

  cursor: pointer;
}

/* MARKER */
/* Chrome/Safari/Opera */
input[type='range'].audio-controller::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 13px;
  width: 13px;

  background: white;

  border: none;
  border-radius: 100%;
}

/* Firefox */
input[type='range'].audio-controller::-moz-range-thumb {
  height: 13px;
  width: 13px;

  background: white;

  border: none;
  border-radius: 100%;
}

/* IE */
input[type='range'].audio-controller::-ms-thumb {
  height: 13px;
  width: 13px;

  background: white;

  border: none;
  border-radius: 100%;
}

input[type='range'].audio-controller.selected {
  opacity: 1;
}

footer.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  padding: 15px;

  background-color: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

footer.footer p a {
  text-decoration: none;
  color: white;
}

footer.footer p a.author {
  font-weight: bold;
}

footer.footer p a.copy:hover {
  cursor: pointer;
  text-decoration: underline;
}

